<template>
    <div class="wp">
        <div class="m-b-5 flex-row-vcenter">
            <el-input
                class=""
                maxlength="100"
                show-word-limit
                v-model="assetNo"
                placeholder="请输入内容"
                clearable
                @input="inputHandler"
                @clear="inputHandler"
            ></el-input>
            <el-button type="primary" icon="el-icon-refresh" class="m-l-10" @click="refreshHandler">刷新</el-button>
        </div>

        <div class="wp-img">
            <el-image class="img" src="./img/example-meter3.jpg" fit="contain"></el-image>
            <svg id="svgId" class="svg-ele"></svg>
        </div>
    </div>
</template>

<script>
import JsBarcode from 'jsbarcode';
import { createCheckCode } from '@/utils/check-code';
export default {
    data() {
        return {
            assetNo: '',
        };
    },
    mounted() {
        this.createCode();
    },
    methods: {
        createCode(value = '') {
            this.$nextTick(() => {
                const val = value ? value : '4130001000000000001095';
                const option = {
                    width: 1.3,
                    height: 30,
                    fontSize: 15,
                    lineColor: '#333',
                    background: '#aab6c4',
                    margin: 2,
                    // font: '',
                    // fontOptions: 'bold',
                };
                JsBarcode(document.getElementById('svgId'), val, option);
            });
        },
        inputHandler() {
            this.createCode(this.assetNo);
        },
        refreshHandler() {
            let value = '41300010' + Date.now();
            this.assetNo = createCheckCode(value);
            this.inputHandler();
        },
    },
};
</script>

<style lang="scss" scoped>
.wp {
    width: 500px;
    height: 100%;
}

.wp-img {
    width: 100%;
    position: relative;
    .img {
        width: 100%;
    }

    .svg-ele {
        position: absolute;
        top: 416px;
        left: 42%;
        transform: translateX(-50%) !important;
    }
}
</style>
